/* ==================== mixines ======================= */
/* ==================== common styles ======================= */
html {
  font-size: 10px;
  overflow-x: hidden;
  /*used for rem-units*/
}

body {
  min-width: 320px;
  color: #fff;
  font-family: "Roboto", sans-serif;
  background: #050505;
  font-size: 16px;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.hidden {
  display: none;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
}

.flex {
  display: -webkit-flex;
  display: flex;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  *zoom: 1;
}

a {
  text-decoration: none;
  color: inherit;
}

img
input:focus,
textarea:focus {
  outline: none;
  border: none;
}

::-webkit-input-placeholder {
  color: #ccc;
  font-size: 12px;
}

::-moz-placeholder {
  color: #ccc;
  font-size: 12px;
}

:-ms-input-placeholder {
  color: #ccc;
  font-size: 12px;
}

::placeholder {
  color: #ccc;
  font-size: 12px;
}

section {
  position: relative;
  padding: 63px 0 20px;
}

.wrapper {
  position: relative;
  width: 97%;
  max-width: 1140px;
  margin: 0 auto;
}

h1,
h2,
h3 {
  font-weight: 700;
}

h2 {
  font-size: 30px;
  margin-bottom: 10px;
  text-transform: uppercase;
  letter-spacing: 3px;
}

h3 {
  font-size: 16px;
}

h4 {
  font-size: 14px;
}

p {
  font-size: 16px;
  line-height: 1.875;
  letter-spacing: .8px;
}

.glare {
  position: absolute;
}

/*------------------------------ header ------------------------------*/
.headerTop {
  justify-content: space-between;
  align-items: center;
  align-content: center;
  padding: 2.9rem 0 1.8rem;
}

#animation_container {
  width: 210px;
  height: 45px;
}

#canvas {
  width: 210px;
  height: 45px;
  position: absolute;
  display: block;
}

#dom_overlay_container {
  display: block;
  pointer-events: none;
  overflow: hidden;
  width: 210px;
  height: 45px;
  left: 0;
  top: 0;
}

.headerTop_contacts {
  font-weight: 300;
  letter-spacing: .3px;
}

.headerTop_contacts span {
  color: #029402;
}

.contacts_links,
.headerTop_lang-list {
  display: inline-block;
}

.contacts_links li,
.headerTop_lang-list li {
  display: inline-block;
  margin-bottom: 10px;
}

.contacts_links li {
  padding: 0 14px;
  border-right: 1px solid #029402;
}

.contacts_links li:last-child {
  border-right: 0;
}

.phoneLink,
.mailLink {
  display: inline-block;
}

.phoneLink:first-of-type:before {
  content: "";
  display: inline-block;
  margin-right: 7px;
  background: url(../img/sprite.png) no-repeat;
  background-position: 0px -145px;
  width: 12px;
  height: 16px;
}

.mailLink {
  font-size: 13px;
  font-weight: 400;
}

.mailLink:before {
  content: "";
  display: inline-block;
  margin-right: 7px;
  background: url(../img/sprite.png) no-repeat;
  background-position: 0px 0px;
  width: 16px;
  height: 11px;
}

.lang_ru {
  display: inline-block;
  background: url(../img/sprite.png) no-repeat;
  background-position: 0px -94px;
  width: 21px;
  height: 15px;
  transition: all .4s;
  opacity: .7;
}

.lang_ru:hover {
  opacity: 1;
}

.lang_en {
  display: inline-block;
  background: url(../img/sprite.png) no-repeat;
  background-position: 0px -69px;
  width: 21px;
  height: 15px;
  transition: all .4s;
  margin-left: 23px;
  opacity: .7;
}

.lang_en:hover {
  opacity: 1;
}

.headerNav {
  background: #029402;
  font-size: 14px;
}

.headerNav .wrapper {
  position: relative;
  min-height: 30px;
}

.headerNav_list {
  justify-content: center;
  align-items: center;
  letter-spacing: 1px;
}

.headerNav_list li {
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.headerNav_list a {
  display: block;
  padding: 15px 1vw;
  transition: all .4s;
  text-transform: uppercase;
  white-space: nowrap;
  background-image: none;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: 50% 45%;
  transition: all .5s;
}

.headerNav_list a:hover {
  background-color: rgba(0, 0, 0, 0.7);
  background-image: url(../img/geometryForms/corners-w.png);
  background-size: 90% 80%;
  color: #0f0;
}

.searchBtn {
  display: inline-block;
  position: absolute;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 16px;
  right: 0;
}

.searchBtn:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

.searchBtn .searchBtn-trigger {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: url(../img/commonImgs/search.png);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  padding: 0;
  transition: all .4s;
  cursor: pointer;
}

.form-wrap {
  width: 500px;
  height: 50px;
}

.search-form {
  display: block;
  width: 0px;
  overflow: hidden;
  position: absolute;
  top: 100%;
  right: 0;
  padding-right: 0px !important;
  z-index: 10;
  opacity: 1;
  border: 1px solid transparent;
  transition: width .5s, border 1s;
}

.search-form.active {
  width: 245px;
  padding-right: 16px;
  border: 1px solid #029402;
}

.search-form.active .search-close {
  opacity: 1;
}

.search-form .search-close {
  opacity: 0;
  position: absolute;
  top: 9px;
  right: 2px;
  transition: all 1s;
  cursor: pointer;
}

.search-form input {
  height: 28px;
  line-height: 28px;
  width: 300px;
  padding: 3px 5px;
  background: transparent;
  color: #fff;
  opacity: 1;
  border: none !important;
  outline: none !important;
}

.mob-nav-btn {
  position: fixed;
  top: 75px;
  right: 30px;
  width: 35px;
  height: 35px;
  background: #050505;
  border: 1px solid #029402;
  cursor: pointer;
  z-index: 10;
  display: none;
}

.mob-nav-btn span {
  display: block;
  background: #029402;
  position: absolute;
  top: 16px;
  left: 5px;
  right: 5px;
  height: 2px;
  border: none;
}

.mob-nav-btn span:before, .mob-nav-btn span:after {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background: #029402;
}

.mob-nav-btn span:before {
  top: -8px;
}

.mob-nav-btn span:after {
  top: 8px;
}

.mob-nav-cont {
  position: fixed;
  top: 0;
  bottom: 0;
  left: -320px;
  width: 320px;
  background: rgba(0, 0, 0, 0.9);
  overflow-y: auto;
  padding: 40px 15px;
  z-index: 11;
  transition: all .7s;
}

.mob-nav-cont.active {
  left: 0;
}

.mob-logo {
  width: 150px;
  display: block;
  margin: 0 auto 25px;
}

.mobNav_list li {
  margin: 20px 0;
}

.mobNav_list li a {
  text-transform: uppercase;
  font-size: 15px;
}

.mob-nav-close {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 20px;
  cursor: pointer;
}

/*------------------------------ banner section ------------------------------*/
.banner-section {
  padding: 0;
}

.banner-section img {
  width: 100%;
}

/*------------------------------ works slider section ------------------------------*/
.worksSlider-section {
  padding-bottom: 3px;
}

.heading-container {
  position: relative;
}

.heading {
  height: 80px;
  line-height: 80px;
  padding-left: 27px;
}

.works-heading {
  margin-bottom: 2px;
}

.hex-container {
  display: block;
  width: 57px;
  height: 59px;
  position: absolute;
  left: -11px;
  top: 9%;
}

.hex {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  transition: all 1s;
}

.sliderContainer {
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
}

.bx-wrapper {
  border: 0 !important;
  box-shadow: none;
}

.sliderArrowsContainer {
  background: #050505;
  position: absolute;
  right: -5%;
  bottom: -4px;
  height: 51px;
  width: 35%;
  transform: skew(-30deg);
  padding: 15px 10px 0px 34px;
  overflow-x: hidden;
}

.sliderArrow {
  display: inline-block;
  width: 66px;
  height: 35px;
  line-height: 35px;
  text-align: center;
  border: 1px solid #029402;
  cursor: pointer;
  transition: all .4s;
  box-shadow: 0px 0px 0px #029402;
}

.sliderArrow:hover {
  filter: brightness(200%);
  box-shadow: 0px 0px 20px #029402;
}

.sliderArrow span {
  display: inline-block;
  transform: skew(30deg);
}

.sliderArrow_prev span {
  background: url(../img/sprite.png) no-repeat;
  background-position: 0px -45px;
  width: 9px;
  height: 14px;
}

.sliderArrow_next span {
  background: url(../img/sprite.png) no-repeat;
  background-position: 0px -21px;
  width: 8px;
  height: 14px;
}

/*------------------------------ geography section ------------------------------*/
.geography-section {
  padding: 6px 0 5px;
}

.mapContainer {
  max-width: 1920px;
  margin: 0 auto;
  background-image: url(../img/earth.jpg);
  background-repeat: no-repeat;
  background-position: 50% 0%;
  background-size: auto 100%;
}

.pinsContainer {
  position: relative;
  width: 550px;
  height: 100vh;
  min-height: 380px;
  max-height: 380px;
  margin: 0 auto;
}

.mapPin {
  display: block;
  position: absolute;
  transition: all .4s;
  cursor: pointer;
}

.mapPin:hover {
  transform: scale(1.2);
  color: white;
  filter: brightness(200%);
  animation: none;
}

.mapPin:hover .pin-img-container:before {
  box-shadow: 0px 0px 10px #029402;
  animation: pinrotate 1.5s linear infinite;
}

.map-link {
  display: block;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
  transition: all .4s;
  cursor: pointer;
}

.map-link:hover {
  transform: scale(1.2);
  color: white;
  filter: brightness(200%);
  animation: none;
}

.pin-img-container {
  display: inline-block;
  position: relative;
  padding: 5px;
  transition: all .4s;
  border-radius: 50%;
}

.pin-img-container:hover {
  box-shadow: 0 0 5px #029402;
}

.pin-img-container img {
  position: relative;
  display: block;
  margin: 0 auto;
}

.pin-img-container:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  border-radius: 50%;
  box-shadow: 0px 0px 10px transparent;
  transition: all .4s;
}

.pin-img-container:after {
  content: "";
  display: block;
  position: absolute;
  bottom: -5px;
  left: 18%;
  width: 26px;
  height: 22px;
  background-image: url(../img/commonImgs/pinlight.png);
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.map-title {
  font-size: 14px;
  display: block;
}

.mob-locations-list {
  -webkit-flex-wrap: wrap;
  -moz-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  -o-flex-wrap: wrap;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-bottom: 25px;
}

.mob-locations-list > li {
  flex-basis: 50%;
  position: relative;
  padding-left: 31px;
}

.mob-locations-list > li:before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 18%;
  left: 0;
  width: 31px;
  height: 42px;
  -webkit-background-size: contain;
  background-size: contain;
  background-image: url(../img/commonImgs/pin-mob.png);
  background-repeat: no-repeat;
  background-position: 50%;
}

.mob-locations-list > li a {
  display: block;
  padding: 15px 10px;
}

.mob-locations-cont {
  display: none;
}

.mob-portfolio-link {
  display: block;
  margin: 0 auto;
  padding: 10px;
  max-width: 350px;
  text-align: center;
  font-size: 16px;
  border: 1px solid #fff;
  margin-bottom: 25px;
}

/*------------------------------ about section ------------------------------*/
.about-section {
