  background-position: 0px -297px;
  width: 46px;
  height: 43px;
}

.adv-icon.puzzle {
  background: url(../img/sprite.png) no-repeat;
  background-position: 0px -350px;
  width: 48px;
  height: 48px;
}

.adv-icon.quality {
  background: url(../img/sprite.png) no-repeat;
  background-position: 0px -197px;
  width: 46px;
  height: 39px;
}

.adv-icon.wallet {
  background: url(../img/sprite.png) no-repeat;
  background-position: 0px -408px;
  width: 46px;
  height: 48px;
}

.advantages-text {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 2.7px;
  line-height: 1.5;
}

.about-text {
  position: relative;
  flex-basis: 50%;
  max-width: 536px;
  padding: 2.5% .4% 1% 8.9%;
}

.about-text p {
  margin-bottom: 4%;
}

.lines-decor-cont {
  position: absolute;
  top: -9px;
  left: 0;
  width: 30px;
  height: 54%;
  max-height: 70vh;
}

.wide-decor-cont,
.narrow-decor-cont {
  position: absolute;
  background-repeat: no-repeat;
}

.wide-decor-cont:after,
.narrow-decor-cont:after {
  content: "";
  display: block;
