.about-glare-l-1 {
  top: 11.3%;
  right: 104.5%;
}

.about-glare-l-2 {
  top: 13.3%;
  right: 104%;
}

.about-glare-r-1 {
  top: 97.4%;
  left: 103.4%;
  filter: blur(1px);
}

.about-glare-r-2 {
  top: 104.3%;
  left: 102.4%;
}

/*------------------------------ contacts-section ------------------------------*/
.contacts-section {
  position: relative;
}

.contacts-container {
  justify-content: space-between;
  max-width: 100%;
}

.contacts-links-part {
  position: relative;
  flex-basis: 50%;
  max-width: 334px;
  padding: 2.3% 3% 3% 9%;
}

.contacts-links-part h4 {
  font-weight: 700;
  margin-bottom: 10px;
  color: #029402;
  text-transform: uppercase;
  letter-spacing: 2.5px;
}

.contacts-links-part .lines-decor-cont {
  height: 95%;
}

.contacts-links-part .lines-decor-cont .narrow-decor-cont {
  height: 80%;
}

.contacts-links-part .lines-decor-cont .wide-decor-cont {
  height: 85%;
}

.contacts-phones,
.contacts-mail,
.contacts-adress {
  position: relative;
  padding-left: 28px;
  margin-bottom: 40px;
}

.contacts-phones:before,
.contacts-mail:before,
.contacts-adress:before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 6px;
  left: 0;
}

.contacts-phones:before {
  background: url(../img/sprite.png) no-repeat;
  background-position: 0px -145px;
  width: 12px;
  height: 16px;
}

.contacts-mail:before {
  background: url(../img/sprite.png) no-repeat;
  background-position: 0px 0px;
  width: 16px;
  height: 11px;
}

.contacts-adress:before {
  background: url(../img/sprite.png) no-repeat;
  background-position: 0px -119px;
  width: 12px;
  height: 16px;
}

.contact-item {
  display: block;
  font-weight: 300;
  line-height: 25px;
}

.tel-item {
  letter-spacing: 1px;
  font-weight: 100;
}

.contacts-form-part {
  position: relative;
  flex-grow: 1;
  padding: 3.1% 0% 3% 21%;
}

.contacts-form-part .main-form {
  -webkit-flex-wrap: wrap;
  -moz-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  -o-flex-wrap: wrap;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: space-around;
  align-content: space-around;
  width: 100%;
}

.contacts-form-part .main-form > div {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: space-between;
  -ms-align-items: center;
  align-items: center;
  padding-top: 17px;
  flex-basis: 100%;
  width: 100%;
}

.contacts-form-part h4 {
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 3.5px;
  margin-bottom: 27px;
}

.contacts-form-part input[type="text"],
.contacts-form-part input[type="tel"],
.contacts-form-part input[type="email"] {
  flex-basis: 45%;
  flex-grow: 0;
  border: none;
  padding: 3px 5px 0;
  width: 255px;
  font-size: 16px;
  height: 28px;
  line-height: 28px;
  color: #fff;
  background: transparent;
  margin-bottom: 27px;
  border-bottom: 1px solid #fff;
  transition: all .4s;
}

.contacts-form-part input[type="text"]:focus,
.contacts-form-part input[type="tel"]:focus,
