.path-container {
  height: 4.5rem;
  line-height: 4.5rem;
  background: #111111;
  background-image: url(../img/geometryForms/horizontalLines.png);
  background-repeat: no-repeat;
  background-position: 50% 0%;
  border-bottom: 1px solid #002700;
}

.path-item {
  font-size: 13px;
  position: relative;
  padding: 5px 22px;
}

.path-item:after {
  content: "";
  display: block;
  position: absolute;
  top: 15%;
  right: 0;
  width: 1px;
  height: 70%;
  background: #029402;
  transform: rotate(20deg);
}

.path-item:first-child {
  padding-left: 0;
}

.path-item:last-child:after {
  display: none;
}

.single-hex-container {
  left: 3px;
}

.download-button {
  max-width: 212px;
}

.arrows-bg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: -30px;
  right: 0;
  width: 100%;
  height: 100%;
  background-image: url(../img/breaks1.png);
  background-position: 100% 2.5%;
  transform: skew(30deg);
  z-index: -5;
}

/*------------------------------ about text section ------------------------------*/
.about-text-section {
  outline: 0;
  padding: 4rem 12rem;
  background-image: url(../img/breaks1.png);
  background-position: 50% 100%;
}

.about-text-section p {
  max-width: 848px;
  font-size: 16px;
  line-height: 1.9;
  margin-bottom: 4rem;
}

/* ==================== css-animations ======================= */
@keyframes blink1 {
  0% {
    filter: brightness(75%);
  }
  30% {
    filter: brightness(500%);
  }
  70% {
    filter: brightness(75%);
  }
  100% {
    filter: brightness(75%);
  }
}

@keyframes lines2 {
  0% {
    bottom: 0%;
  }
  5% {
    bottom: 30%;
  }
  10% {
    bottom: 38%;
  }
  15% {
    bottom: 28%;
  }
  20% {
    bottom: 48%;
  }
  25% {
    bottom: 35%;
  }
  30% {
    bottom: 45%;
  }
  35% {
    bottom: 30%;
  }
  40% {
    bottom: 50%;
  }
  45% {
    bottom: 55%;
  }
  50% {
    bottom: 50%;
  }
  55% {
    bottom: 57%;
  }
  60% {
    bottom: 50%;
  }
  65% {
    bottom: 62%;
  }
  70% {
    bottom: 60%;
  }
  75% {
    bottom: 68%;
  }
  80% {
    bottom: 88%;
  }
  85% {
    bottom: 100%;
  }
  90% {
    bottom: 42%;
  }
  95% {
    bottom: 50%;
  }
  100% {
    bottom: 0%;
  }
}

@keyframes brightness {
  0% {
    filter: brightness(100%);
  }
  80% {
    filter: brightness(100%);
  }
  50% {
    filter: brightness(300%);
  }
  80% {
    filter: brightness(100%);
  }
  100% {
    filter: brightness(100%);
  }
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
    opacity: 1;
  }
  50% {
    transform: rotate(180deg);
    opacity: 0;
  }
  100% {
    transform: rotate(360deg);
    opacity: 1;
  }
}

@keyframes pinrotate {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}

@keyframes huerotate {
  0% {
    filter: grayscale(100%);
  }
  100% {
    filter: grayscale(0%);
  }
}

